$light-green:#AFFF00;
$light-red:#fd1216;

.Header{
    position: sticky;
    display: flex;
    transition: opacity 500ms ease-in-out,background 500ms ease-in-out;
    top:0;
    z-index: 100;
    height: 70px;
    width: 100%;
    align-items: center;
    span{
        cursor: pointer;
        font-size: 15px;
        font-family:"Poppins" ,sans-serif;
        font-weight: 300 !important;
    }

    background-color: rgba(0,0,0,0.8);
    // backdrop-filter: blur(15px);
    button{
        background: transparent;
        border:none;
    }
}
.header-inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 1300px;
    max-width: 90vw;
    margin: auto;
}
.Header-logo{
    cursor:pointer;
    display: flex;
    align-items: center;
    img{
        height: 50px;
        margin: 0px 15px 0px 0px;
    }
    span{
        font-size: 30px;
        color: #c29f45 !important;
    }
}
.header-mint-btn{
    height: 36px;
    width: 99px;
    background-color: $light-green;
    border:none;
    cursor: pointer;
    box-shadow: 0px 0px 5px 1px $light-green;

}
.Header-mobile{
    display: none;
}
.header-menu-icn{
    display: none;
}
.Header-right-nav{
    display: flex;
    align-items: center;
    span{
        // text-shadow: 0px 0px 8px white;
        letter-spacing: 2px;
        margin-left: 1rem;
    }
}
.header-social-holder{
    position: absolute;
    // margin-top: 2rem;
    bottom:140px;
    width:200px;
    display: flex;
    justify-content: space-between;
    margin: auto;
    left:0;
    right: 0;
}
.social-wrapper{
    display: flex;
    align-items: center;
    margin: 0 0 0 2rem;
}





.header-hamburger{
    
    z-index: 20;
    display: none;
    justify-content: center;
    align-items: center;
    height: 55px;
    width: 55px;
    cursor: pointer;

}
.header-hamburger-inner{
    display: flex;
    justify-content: center;
    position: relative;
    height: 40px;
    width: 40px;
}

.hamburger-top,.hamburger-bottom{
    height: 2px ;
    width: 60%;
    background-color: white;
    border-radius: 1px ;
}
.hamburger-top{
    position: absolute;
    top:14px;
}
.hamburger-bottom{
    position: absolute;
    bottom:14px;
}
.header-mobile-nav{
    display: none;
    z-index: 15;
    top:0;
    left:0;
    position: absolute;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.518);
    backdrop-filter:blur(25px) !important;
    overflow: scroll;
    opacity: 0;
    transition:all 350ms ease-in-out;
}
.header-mobile-nav-inner{
    margin:40px auto 0 auto;
    display: flex;
    flex-direction: column;
    // width: 600px;
    width: 86vw;
    // background-color: green;
}
.header-mobile-logo{
    margin:auto;
    height: 120px;
    width: 120px;
}
.mobile-nav-button{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width:100%;
    height: 50px;
    // background-color: red;
    border-bottom: 1px solid #7A7570 !important;
    img{
        height: 18px;
    }
    font-weight: bold;
}
.social-icons{
    padding-left: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.icons{
    width: 30px;
    height: 30px;
    background-image: linear-gradient(#FFC603, #ff7c00);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    margin-right: 5px ;
    margin-left: 0px !important;
}
.icons i{
    width: 28px;
    height: 28px;
    background-color: #001;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    // padding:0 12px ;
    padding-left: 50%;
}

@media screen and (max-width:1080px) {
    .Header{
     
        padding: 0px 10px 0 10px;
  
    }



    .Header-right-nav{
        display: none;
    }
    .header-hamburger{
        display: flex;
    }


}
@media screen  and (max-width:500px){
    .Header-logo{
      
        span{
            font-size: 20px;
            color: #c29f45 !important;
        }
    }
    .Header{
       
        height: 65px;
    
    }
}