.team-card{
    margin: 20px 0;
}
.team-profile-box{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
   
    /* background-color: red; */
}
.team-avater{
    position: relative;
    width: 200px;
    height: 200px;
    margin-bottom: -75px !important;
    border-radius: 100000px;  
    /* margin: unset auto -75px auto !important; */
  
    /* margin-left:auto; */
}
.blank{
    height: 95px;
    width:100%;
    background-color: #212121;
}
.team-des{
    background-color: #121212;
    text-align: center;
}
.first-4{
    min-height: 270px;
}
.teams{
    position: relative;
}
.teams::before{
    position: absolute;
    content: '';
    width: 150px;
    height: 150px;
    border-radius: 50%;
    left:0;
    top: 100px;
    background: rgba(239, 7, 88, 0.374);
    filter: blur(100px);
    z-index:1;
}
.teamsEffect::before{
    position: absolute;
    content: '';
    width: 150px;
    height: 200px;
    border-radius: 50%;
    right:50px;
    bottom: 0px;
    background: rgba(24, 199, 185, 0.374);
    filter: blur(110px);
    z-index:1;
}

.member-name {
    font-size: 25px;
    color: #FFC508;
}
@media screen and (max-width:500px) {
    .team-des{
        padding: 20px 20px 10px 20px;
        margin: auto;
    }
    .first-4{
        min-height: unset;
        height: fit-content;
    }
}