
#faq{
    h2{
        font-size: 3rem !important;
    }
}
.accordion-body{
    background-color: #121212 !important;
    color: #fff !important;
    text-align: left;
}
.question{
    font-size: 22px;

}
.question span{
    color: #aaa;
    margin-right: 10px;
   
}
.questionAnswer{
    color: #efefef;
}
.accurdian-icon{
    background-color: #fff !important;
    z-index: 22 !important;
}
@media screen  and (max-width:500px){
    #faq{
        h2{
            font-size: 2.4rem !important;
        }
    }
    .question{
        font-size: 16px;
        width: 100%;
        text-align: start;
        margin-left: -15px;
    }
    .questionAnswer{
        text-align: justify !important;
    }
}

