    margin-top: 15px;
    color: white;

    
}

.home-long-button{
    // top:0;
    position: fixed !important;
    z-index: 100000;
    left:0;
    right: 0;
    margin: auto;
    width: fit-content;
    height: fit-content;
    bottom: 40px;
    box-shadow: 0px 0px 10px rgba(0,0,0);

}
.effect-perple{
    position: relative;
}
.effect-perple::before{
    position: absolute;
    left: 0;
    bottom: 0;
    content: '';
    width: 40%;
    height: 40%;
    background-color: rgb(255, 111, 0);
    filter: blur(400px);
    transform: rotate(21.75deg);
    z-index: -1;
}
.effect-blue{
    position: relative;
}
.effect-blue::after{
    position: absolute;
    content: '';
    width: 300px;
    height: 40%;
    right: 12%;
    bottom: 30px;
    background: rgba(115, 83, 229, 0.286);
    filter: blur(50px);
    z-index: -1;
}
.main-video{
    margin: 3rem  auto 4rem auto;
    width:1015px;
    max-width: 90vw;
    video{
        width: 100%;
    }
}
